/**  Hero  **/

#hero {
    overflow: hidden;

    min-height: 600px;
}

#hero .scroll,
#content_hero .scroll {
    position: absolute;
    z-index: 1;
    bottom: 0;
    left: 50%;

    margin-left: -45px;

    -webkit-animation: ScrollDown 1.5s infinite;
            animation: ScrollDown 1.5s infinite;
}

#hero .carousel-inner,
#hero .carousel-inner .item {
    height: 100%;
    min-height: 600px;

    text-align: center;

    background-color: #000;
    background-position: center;
    background-size: cover;
}

#hero .carousel-inner .item:before,
#content_hero:before {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    display: block;

    content: '';

    background-image: -webkit-gradient(
    linear,
    right bottom,
    right top,
    color-stop(0, rgba(0, 0, 0, 0)),
    color-stop(1, rgb(0, 0, 0))
    );
    background-image:      -o-linear-gradient(top, rgb(0, 0, 0) 0%, rgba(0, 0, 0, 0) 100%);
    background-image: -webkit-linear-gradient(top, rgb(0, 0, 0) 0%, rgba(0, 0, 0, 0) 100%);
    background-image:         -webkit-gradient(linear, left bottom, left top, from(rgb(0, 0, 0)), to(rgba(0, 0, 0, 0)));
    background-image:         -webkit-linear-gradient(bottom, rgb(0, 0, 0) 0%, rgba(0, 0, 0, 0) 100%);
    background-image:         -o-linear-gradient(bottom, rgb(0, 0, 0) 0%, rgba(0, 0, 0, 0) 100%);
    background-image:         linear-gradient(to top, rgb(0, 0, 0) 0%, rgba(0, 0, 0, 0) 100%);
}

#hero .container {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    display: -ms-flexbox;

    display: flex;

    margin: auto;

    -webkit-transform: none;
            -ms-transform: none;
        transform: none;

    -ms-flex-align: center;

        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

#hero .container .blurb,
#content_hero .container .blurb {
    position: relative;

    width: 100%;
    margin-top: 50px;

    -webkit-animation: Blurb .5s .5s forwards;
            animation: Blurb .5s .5s forwards;
    text-align: left;

    opacity: 0;
}

#hero .container .blurb h1,
#content_hero .container .blurb h1 {
    font-size: 52px;
    font-weight: 300;

    padding: 0;

    -webkit-animation: Heading .5s .5s forwards;
            animation: Heading .5s .5s forwards;
    letter-spacing: 3px;

    opacity: 0;
    color: #fff;
    border: none;
}

#hero .container .blurb .buttons {
    -webkit-animation: Buttons .5s .5s forwards;
            animation: Buttons .5s .5s forwards;

    opacity: 0;
}

#hero .container .blurb p,
#content_hero .container .blurb p {
    font-size: 18px;

    color: #fff;
}

#hero .container .blurb .certificate,
#content_hero .container .blurb .certificate {
    font-size: 13px;
    font-weight: bold;
    line-height: 48px;

    display: inline-block;

    width: 48px;
    height: 48px;
    margin-right: 8px;

    -webkit-transform: translateY(2px);

        -ms-transform: translateY(2px);

            transform: translateY(2px);
    text-align: center;

    color: #fff;
    border: solid 2px #fff;
    border-radius: 50%;
}

