/** Buttons **/

.btn {
    font-size: 13px;
    font-weight: normal;

    position: relative;

    overflow: hidden;

    width: auto;
    padding: 12px 24px;

    letter-spacing: 4px;
    text-transform: uppercase;

    color: #fff;
    border-width: 2px;
    border-radius: 30px;
}

.btn i,
.btn:hover i {
    position: relative;
    z-index: 1;

    display: -ms-inline-flexbox;

    display: inline-flex;

    vertical-align: middle;

    color: #fff;
}

.btn-default {
    border: none;
    background-image: -webkit-gradient(linear, left top, right top, from(#faa82f), to(#e76115));
    background-image: -webkit-linear-gradient(left, #faa82f, #e76115);
    background-image: -o-linear-gradient(left, #faa82f, #e76115);
    background-image: linear-gradient(to right, #faa82f, #e76115);
}

.btn-default span,
.btn-default span:hover,
.btn-ghost span:hover {
    position: relative;
    z-index: 1;

    color: #fff;
}

.btn-default:hover:before {
    width: 140%;
}

.btn-default:before,
.btn-ghost:before {
    position: absolute;
    z-index: 0;
    bottom: 0;
    left: -15px;

    width: 0;
    height: 100%;

    content: '';
    -webkit-transition: .4s;
            -o-transition: .4s;
            transition: .4s;
    -webkit-transform: skewX(30deg);
        -ms-transform: skewX(30deg);
            transform: skewX(30deg);

    background: #e76115;
}

.btn-primary {
    border: none;
    background: #e76115;
}

.btn-ghost {
    -webkit-transition: all 300ms ease-in-out;
            -o-transition: all 300ms ease-in-out;
            transition: all 300ms ease-in-out;

    color: #e76115;
    border-color: #e76115;
    background: transparent;
}

.btn-ghost:hover span {
    color: #fff;
}

.btn-ghost:hover:before {
    z-index: -1;

    width: 140%;
}

.btn-ghost:hover {
    color: #fff;
}

button.btn-default,
button.btn-primary {
    -webkit-transition: all 300ms ease-in-out;
            -o-transition: all 300ms ease-in-out;
            transition: all 300ms ease-in-out;
}

button.btn-default:before,
button.btn-primary:before {
    display: none;
}

button.btn-default:hover,
button.btn-primary:hover {
    color: #fff;
    background: #faa82f;
}

a.arrow-button {
    font-size: 12px;

    position: relative;

    letter-spacing: 4px;
    text-transform: uppercase;

    color: #e76115;
}

a.arrow-button:after {
    font-family: 'Material Icons';
    font-weight: bold;

    position: absolute;
    top: -1px;

    content: 'chevron_right';
    text-transform: none;

    -webkit-font-feature-settings: 'chevron_right';
}

a.arrow-button:hover {
    color: #101010;
}

/**  Movies slider  **/

 
.slick-slide .movie-poster:hover:before {
    width: 200%;
}

.slick-slide .movie-poster:hover aside {
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);

    opacity: 1;
}

.slick-slide .movie-poster aside {
    position: absolute;
    z-index: 1;

    display: -ms-flexbox;

    display: flex;

    width: 100%;
    height: 100%;

    -webkit-transition: .3s;
            -o-transition: .3s;
            transition: .3s;
    -webkit-transition-delay: .1s;
            -o-transition-delay: .1s;
       transition-delay: .1s;
    -webkit-transform: scale(.9);
        -ms-transform: scale(.9);
            transform: scale(.9);
    text-align: center;

    opacity: 0;

    -ms-flex-align: center;

        align-items: center;
}

.slick-slide .movie-poster aside div {
    width: 100%;
}

.slick-slide .movie-poster aside .play {
    font-size: 32px;
    line-height: 70px;

    display: block;

    width: 80px;
    height: 80px;
    margin: 0 auto 20px auto;

    color: #fff;
    color: #fff;
    border: solid 2px #fff;
    border-radius: 50%;
}

.slick-slide .movie-poster aside .play:hover {
    -webkit-transform: scale(.9);
        -ms-transform: scale(.9);
            transform: scale(.9);
}

.slick-slide .movie-poster aside .read-more {
    font-size: 13px;

    display: block;

    margin-bottom: 10px;

    cursor: pointer;
    letter-spacing: 4px;
    text-transform: uppercase;

    color: #fff;
}

.slick-slide .movie-poster aside .date {
    font-size: 16px;

    display: block;

    color: #fff;
}

.star-rating {
