}

/**  Tabs  **/

.tabs ul {
    font-size: 0;

    margin: 0;
    padding: 0;

    border-bottom: 1px solid #d8d8d8;
}

.tabs ul li {
    display: inline-block;
}

.tabs ul li a {
    font-size: 18px;

    position: relative;

    display: block;

    padding: 10px 30px 15px 30px;

    letter-spacing: 1px;
    text-transform: uppercase;

    color: #4a4a4a;
}

.tabs ul li.ui-state-active a {
    color: #e76115;
}

.tabs ul li a:after {
    position: absolute;
    bottom: -1px;
    left: 0;

    display: block;

    width: 0;
    height: 4px;

    content: '';
    -webkit-transition: all .25s ease-in-out;
            -o-transition: all .25s ease-in-out;
            transition: all .25s ease-in-out;

    background-image: -webkit-gradient(linear, left top, right top, from(#faa82f), to(#e76115));

    background-image: -webkit-linear-gradient(left, #faa82f, #e76115);

    background-image: -o-linear-gradient(left, #faa82f, #e76115);

    background-image: linear-gradient(to right, #faa82f, #e76115);
}

.tabs ul li a:hover:after,
.tabs ul li.ui-state-active a:after {
    width: 100%;
}

.tabs > div {
    padding: 45px 0;
}

.tabs ul li.date {
    float: right;
}

.tabs ul li.date span {
    font-size: 14px;

    display: block;

    padding: 10px 0;
    padding-right: 0;

    cursor: default;
    text-align: right;
    text-transform: uppercase;

    color: #717171;
}

/**  Pill tabs  **/

.tabs.pill-style ul {
    border: none;
    border-radius: 30px;
    background: #f5f5f5;
}

.tabs.pill-style ul li a {
    padding: 10px 40px;

    text-decoration: none;

    border-radius: 30px;
}

.tabs.pill-style ul li a:after {
    display: none;
}

.tabs.pill-style ul li.ui-state-active a {
    color: #fff;
    background-image: -webkit-gradient(linear, left top, right top, from(#faa82f), to(#e76115));
    background-image: -webkit-linear-gradient(left, #faa82f, #e76115);
    background-image: -o-linear-gradient(left, #faa82f, #e76115);
    background-image: linear-gradient(to right, #faa82f, #e76115);
}

/**  Accordion  **/

.accordion h2,
.accordion h3,
.accordion h4 {
    font-size: 18px;
    font-weight: 400;

    margin-bottom: 20px;
    padding: 0 15px;
    padding-bottom: 10px;

    cursor: pointer;
    text-transform: uppercase;

    color: #4a4a4a;
}

.accordion h2:after,
.accordion h3:after,
.accordion h4:after {
    display: none;
}

.accordion h2.ui-state-active,
.accordion h3.ui-state-active,
.accordion h4.ui-state-active {
    color: #e76115;
}

.accordion h2.ui-state-active:before,
.accordion h3.ui-state-active:before,
.accordion h4.ui-state-active:before {
    content: '\f068';
}

.accordion h2:before,
.accordion h3:before,
.accordion h4:before {
    font-family: FontAwesome;
    font-size: 12px;

    position: absolute;
    right: 15px;

    display: inline-block;

    content: '\f067';

    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.accordion div {
    padding: 30px 15px;
}

/**  Pill accordion  **/

.accordion.pill-style h2,
.accordion.pill-style h3,
.accordion.pill-style h4 {
    margin: 0;
    padding: 15px;

    color: #717171;
    background: #f5f5f5;
}

.accordion.pill-style h2:before,
.accordion.pill-style h3:before,
.accordion.pill-style h4:before {
