}

@media (max-width: 600px) {
    #hero .container .blurb h1,
    #content_hero .container .blurb h1 {
        font-size: 40px;
        line-height: 1.2em;
    }

    #content_hero .star-rating {
        margin-top: 10px;
    }

    .slick-slide .movie-poster:before {
        -webkit-transform: skewX(23deg);
            -ms-transform: skewX(23deg);
                transform: skewX(23deg);
    }

    .slick-slide img,
    .movie-tabs img {
        width: 100%;
    }

    .tabs ul {
        border-bottom: none;
    }

    .tabs ul li {
        display: block;
    }

    .tabs ul li a {
        padding: 10px 30px;
    }

    .tabs ul li a:after {
        width: 4px;
        height: 0;

        background-image: -webkit-gradient(linear, left top, left bottom, from(#faa82f), to(#e76115));

        background-image: -webkit-linear-gradient(top, #faa82f, #e76115);

        background-image: -o-linear-gradient(top, #faa82f, #e76115);

        background-image: linear-gradient(to bottom, #faa82f, #e76115);
    }

    .tabs ul li a:hover:after,
    .tabs ul li.ui-state-active a:after {
        width: 4px;
        height: 100%;
    }

    .tabs ul li:last-of-type {
        display: block;
        float: none;
    }

    .tabs ul li:last-of-type span {
        text-align: left;
        text-indent: 30px;
    }

    .movie-tabs .viewing-times {
        display: block;

        margin-bottom: 25px;
    }

    .movie-tabs .running-time {
        padding-top: 10px;

        text-align: left;
    }

    .comingSoon-slides .single-slide {
        display: block;
        overflow: hidden;
    }

    .comingSoon-slides .video {
        margin: 40px 0 0 0;
    }

    .single-movie .poster {
        width: 100%;
    }

    .single-movie .share {
        text-align: left;
    }

    .single-movie .share a {
        font-size: 18px;
        line-height: 40px;

        width: 43px;
        height: 43px;
    }

    .icon-row {
        width: 100%;

        text-align: center;
    }

    .icon-row .col {
        display: block;
    }

    .icon-row .col:first-of-type {
        padding: 0;
    }

    .icon-row .col .circle {
        margin: 0 auto 25px auto;
    }

    ul.pagination li {
        display: none;
    }

    ul.pagination li:first-of-type,
    ul.pagination li:last-of-type {
        display: inline-block;
    }

    .single-tags {
        float: none;

        margin-bottom: 10px;
    }

    .social-share {
        float: none;
    }

    .social-share .share {
        text-align: left;
    }

    .form-group.half {
        float: none;

        width: 100%;
    }

    .form-group.half:first-of-type,
    .form-group.half:last-of-type {
        padding: 0;
    }

    .icon-box {
        margin-bottom: 30px;
    }

    .tabs.pill-style ul li a {
        border-radius: 0;
    }

    .tabs.pill-style ul li:first-of-type a {
        border-radius: 30px 30px 0 0;
    }

    .tabs.pill-style ul li:last-of-type a {
        border-radius: 0 0 30px 30px;
    }
}



